@font-face {
    font-family: "Inter";
    src: url("./assets/Inter-VariableFont_slnt\,wght.ttf") format("embedded-opentype"),
        url("./assets/Inter-VariableFont_slnt\,wght.ttf") format("woff2"),
        url("./assets/Inter-VariableFont_slnt\,wght.ttf") format("woff"),
        url("./assets/Inter-VariableFont_slnt\,wght.ttf") format("truetype"),
        url("./assets/Inter-VariableFont_slnt\,wght.ttf") format("svg");
}

$Gray: rgb(63, 78, 101);
$Green: #1fd85d;
$White: white;
$Yel: #ffc85e;
$Black: black;
$SemiBlack: rgb(43, 28, 1);
$GradYel: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
$SemiWithe: #ededed;
$SemiGrey: #5c6370;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Inter", "Times New Roman", serif;
    font-style: normal;
}
// Текст //

p.stock,
p.Page__text,
p.tel {
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
}
p.time,
p.description {
    font-weight: 300;
    font-size: 12px;
    line-height: 150%;
}
p.time,
p.tel {
    margin: 0;
    padding: 0;
}
p.description {
    color: $Gray;
}
p.stock {
    color: $Green;
    margin-bottom: 10px;
}
p.Page__text {
    color: $Gray;
    margin-bottom: 50px;
}
p.phone {
    font-weight: 400;
    font-size: 10px;
    line-height: 150%;
    text-decoration-line: underline;
    margin: 0;
    padding: 0;
}

.good__name,
.good__name:active,
.good__name:hover {
    text-decoration: none;
    width: 100%;
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 5px;
    color: $Black;
    cursor: pointer;
}
p.thanks__text {
    font-weight: 300;
    font-size: 20px;
    line-height: 120%;
    color: $Gray;
}
p.back {
    display: none;
}
h1.content__name {
    font-weight: 800;
    font-size: 30px;
    line-height: 150%;
    margin-bottom: 10px;
}
h1.Page__header {
    font-weight: 500;
    font-size: 40px;
    line-height: 120%;
    text-transform: uppercase;
}
h2.price {
    font-weight: 800;
    font-size: 30px;
    line-height: 130%;
}
h3.positions__subheader,
h4.positions__subheader {
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
}
h3.positions__subheader {
    text-transform: uppercase;
    margin-bottom: 10px;
}

// Разметка страницы //
.Header {
    min-width: 100vw;
    width: 100%;
    height: 170px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: $White;
    .Header__Up {
        width: 100%;
        height: 60px;
        border-bottom: 1px solid rgba($Gray, 0.3);
        display: flex;
        justify-content: center;
        .Header__content {
            padding: 15px 280px;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
        }

        .Header_address {
            max-width: 1920px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
            gap: 40px;
            .address {
                width: 240px;
                position: relative;
                margin-left: 30px;
            }
            .address::before {
                content: "";
                width: 15px;
                height: 15px;
                position: absolute;
                background-size: contain;
                background-repeat: no-repeat;
                top: 10px;
                left: -30px;
            }

            .address:first-child::before {
                background-image: url(./assets/map.png);
            }
            .address:last-child::before {
                background-image: url(./assets/mail.png);
            }
        }
        .Header__nav {
            .nav {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: row;
                gap: 60px;
            }
            p {
                cursor: pointer;
            }
        }
    }

    .Header__Down {
        width: 100%;
        height: 110px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        border-bottom: 1px solid rgba($Gray, 0.3);
        .btn__menu {
            display: none;
        }
        .Header__content {
            padding: 15px 280px;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
            gap: 35px;

            .Header__call {
                display: flex;
                justify-content: baseline;
                align-items: center;
                gap: 10px;
                p {
                    text-align: end;
                }
                img {
                    width: 75px;
                    border-radius: 37px;
                }
            }
        }
    }
    .Mobile__Down {
        display: none;
    }
}
.UpFilter {
    width: 100%;
    padding: 0 280px;
    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-direction: column;
    gap: 25px;
    margin-bottom: 50px;
    margin-top: 45px;
    .UpFilter__sort {
        width: 100%;
        max-width: 1920px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        .sort {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row;
            font-weight: 500;
            font-size: 16px;
            .sort__select {
                border-width: 0px;
            }
        }
    }
    ul.Filter__list {
        width: 100%;
        max-width: 1920px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
        li {
            width: 9%;
            display: inline-block;
            text-align: center;
            padding: 9px;
            vertical-align: center;
            list-style-type: none;
            background: $White;
            box-shadow: 0px 15px 70px -11px rgba($SemiBlack, 0.04);
            border-radius: 10px;
            color: $Gray;
            font-size: 14px;
            line-height: 17px;
        }
    }
    h2.Page__subheader {
        display: none;
    }
}
.SideMenu {
    width: 13%;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
    .SideMenu__filter {
        display: flex;
        justify-content: start;
        align-items: start;
        flex-direction: column;
        gap: 20px;
        .price__filter {
            display: flex;
            align-items: baseline;
            justify-content: space-between;
            flex-direction: column;
            gap: 15px;
            .price__box {
                display: flex;
                align-items: baseline;
                justify-content: space-between;
                flex-direction: row;
                gap: 10px;
                input[type="number"]::-webkit-outer-spin-button,
                input[type="number"]::-webkit-inner-spin-button {
                    -webkit-appearance: none;
                }
                input[type="number"],
                input[type="number"]:hover,
                input[type="number"]:focus {
                    appearance: none;
                    -moz-appearance: textfield;
                }
                input.price__input {
                    padding: 4px 14px;
                    gap: 10px;
                    width: 100px;
                    border: none;
                    text-align: center;
                    background: $GradYel;
                    border-radius: 1000px;
                }
            }
        }
        .check__filter {
            display: flex;
            justify-content: center;
            align-items: start;
            flex-direction: column;
            gap: 15px;
            .filter {
                width: 100%;
                position: relative;
                display: flex;
                justify-content: start;
                align-items: baseline;
                flex-direction: row;
                label {
                    position: absolute;
                    z-index: 1;
                    width: 100%;
                    top: 0;
                    left: 0;
                    padding-left: 20px;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 90%;
                    color: $Gray;
                }
            }
        }
        .check__filter:nth-child(3) {
            padding-bottom: 20px;
            border-bottom: 1px dashed rgba($Gray, 0.3);
        }
    }

    .SideMenu__btns {
        display: flex;
        justify-content: center;
        align-items: start;
        flex-direction: row;
        gap: 10px;
        margin-bottom: 40px;
    }

    .more {
        width: 100%;
        font-weight: 500;
        font-size: 12px;
        line-height: 150%;
        color: $Gray;
        cursor: pointer;
    }
    .more::after {
        content: "▼";
        width: 5px;
        height: 5px;
        margin-left: 5px;
    }
    .SideMenu__positions {
        width: 100%;
        .check__filter {
            display: flex;
            justify-content: center;
            align-items: start;
            flex-direction: column;
        }
        ul.positions {
            margin: 20px auto;
            border-bottom: 1px dashed rgba($Gray, 0.3);
            li {
                margin: 5px 0;
                list-style-type: none;
                font-weight: 400;
                font-size: 14px;
                line-height: 150%;
                color: $Gray;
            }
            li:last-child {
                margin-bottom: 20px;
            }
        }
        .SideMenu__brend {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: start;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 20px;
        }
    }
}
.Catalog {
    width: 100%;
    margin: 0px 280px;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: row;
    gap: 100px;

    .Page__goods {
        width: 55%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 50px;
        .Goods {
            width: 100%;
            display: flex;
            justify-content: start;
            align-items: center;
            flex-direction: row;
            gap: 20px;
            flex-wrap: wrap;
            .Good__position {
                padding: 30px 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                width: 330px;
                height: 490px;
                gap: 10px;
                .good__img {
                    height: 40%;
                    border-radius: 4px;
                }

                .good__price {
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    flex-direction: row;
                    p {
                        font-weight: 800;
                        font-size: 16px;
                    }
                }
            }
        }
        .page__box {
            display: flex;
            flex-direction: row;
            gap: 35px;
            align-items: baseline;
            justify-content: center;

            .Page__num {
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-direction: row;
                gap: 20px;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                text-align: center;
                color: $Gray;
                margin-bottom: 20px;
                cursor: pointer;

                .Page__active {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 35px;
                    height: 35px;
                    border-radius: 50%;
                    background: $GradYel;
                    margin: -10px;
                    cursor: pointer;
                }
            }
            .page__arrow {
                border: none;
                color: $Yel;
                width: 9px;
                height: 16px;
                cursor: pointer;
            }
        }
    }
}
.GoodsPage {
    width: 100%;
    margin: 0 0 60px 0;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
    gap: 65px;
    padding: 0 280px;
    .Goods__content {
        width: 100%;
        display: flex;
        justify-content: start;
        align-items: start;
        flex-direction: row;
        gap: 40px;
        img {
            max-width: 665px;
            width: auto;
            max-height: 770px;
            height: auto;
        }
        .content__info {
            width: 45%;
            display: flex;
            justify-content: space-between;
            align-items: start;
            flex-direction: column;

            .content__price {
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-direction: row;
                gap: 40px;
            }
            .content__action {
                padding-left: 30px;
                margin-bottom: 50px;
                margin-top: 40px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-direction: row;
                gap: 80px;
                img {
                    width: 20px;
                    height: 20px;
                }
                .btn {
                    background: $White;
                    p {
                        color: $Gray;
                    }
                }
            }
            .content {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: start;
                flex-direction: column;
                flex-wrap: wrap;
                gap: 15px;
                padding-bottom: 20px;
                border-bottom: 1px dashed rgba($Gray, 0.3);
                margin-top: 25px;
            }
            .content:last-child {
                border: none;
            }
        }
    }
}
.CartPage {
    width: 100%;
    padding: 0 280px;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
    gap: 50px;
    .Cart__position {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        padding: 50px 0px;
        img {
            width: 191px;
            height: 281px;
        }
        .Cart__info {
            width: 480px;
            display: flex;
            justify-content: start;
            align-items: start;
            flex-direction: column;
            gap: 10px;
            margin: 0px 120px 0 20px;
        }
        .count {
            padding: 0 45px;
            border-right: 1px dashed rgba($Gray, 0.2);
            border-left: 1px dashed rgba($Gray, 0.2);
        }
        .price {
            display: inline-block;
            font-weight: 700;
            font-size: 20px;
            line-height: 130%;
            padding: 0 45px;
            border-right: 1px dashed rgba($Gray, 0.2);
        }
        .btn__del {
            margin-left: 40px;
        }
    }
    .Cart__total {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        flex-direction: row;
        margin-bottom: 100px;
    }
}
.Thanks {
    text-decoration: none;
    position: fixed;
    color: $Black;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba($Black, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    .Thanks__box {
        position: relative;
        width: 735px;
        height: 424px;
        background: $White;
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 20px;
        text-align: center;
        img {
            width: 60px;
            height: 60px;
        }
        .close {
            position: absolute;
            top: 35px;
            right: 35px;
            color: $Yel;
            border: none;
        }
    }
}
.admin__box {
    width: 100%;
    margin: 50px;
    box-shadow: 0px 0px 10px 2px rgba($SemiBlack, 0.7);
    padding: 20px;
    border: 1px solid rgba($SemiBlack, 0.7);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    justify-content: space-between;
    align-items: center;
    .admin__position {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: baseline;
        border-bottom: 1px dashed rgba($SemiBlack, 0.7);
        padding: 5px;
        input {
            width: 60%;
            border-radius: 4px;
            padding: 4px;
        }
    }
}
.Footer {
    padding: 70px 280px 120px 280px;
    background: $Gray;
    box-shadow: 0px 15px 70px -11px rgba($SemiBlack, 0.04);
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-direction: row;
    .Footer__col {
        max-width: 320px;
        display: flex;
        justify-content: start;
        align-items: start;
        flex-direction: column;
        gap: 15px;
        color: $White;
        h3 {
            font-weight: 600;
            font-size: 20px;
            line-height: 150%;
            margin-bottom: 25px;
        }
        h4 {
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
        }

        ul {
            margin-left: 0;
            padding-left: 0;

            li {
                list-style-type: none;
                margin-bottom: 20px;
            }
        }
        .Icon__Box {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
            gap: 5px;
            img.Social {
                width: 39px;
                height: 39px;
            }
            img.PayCard {
                width: 61px;
                height: 39px;
            }
        }
    }
    .Mobile__footer {
        display: none;
    }
}

// Универсальные блоки //
.show {
    display: flex;
}
.showUl {
    display: block;
    cursor: pointer;
}
.showMob {
    display: flex;
    position: relative;
    z-index: 20;
}
.hide {
    display: none !important;
}
.map__nav {
    width: 100%;
    margin-bottom: 25px;
    margin-top: 45px;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: start;
    ul {
        width: 100%;
        max-width: 1920px;
        gap: 20px;
        li {
            display: inline-block;
            text-align: center;
            list-style-type: none;
            background: $White;
            font-size: 14px;
            line-height: 17px;
            border-right: 1px dashed rgba($Gray, 0.3);
            padding: 0px 10px;
        }
        li:last-child {
            border: none;
            color: rgba($Gray, 0.5);
        }
    }
}
.inp__box {
    width: 270px;
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: $SemiWithe;
    border-radius: 36px;
    color: $SemiGrey;
    padding: 0 20px;
    input {
        width: 210px;
        height: 60px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        background: $SemiWithe;
        border-radius: 36px;
        padding: 20px;
        color: $SemiGrey;
        border: none;
        margin-left: -20px;
    }
    p {
        margin: 20px;
        font-size: 12px;
        opacity: 0.6;
    }
    .inp__icon {
        width: 40px;
        height: 40px;
        background: $Yel;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        .btn {
            width: 40px;
            height: 40px;
            border: none;
        }
    }
}
.logo__box {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7px;
    position: relative;
    cursor: pointer;
    img.logo {
        width: 49px;
        height: 66px;
    }
    img.logo1,
    img.logo2 {
        width: 7px;
        height: 2px;
        position: absolute;
        top: 36px;
        left: 15px;
    }
    img.logo2 {
        left: 27px;
    }
    .logo__name {
        display: flex;
        justify-content: center;
        align-items: center;
        img.logo {
            width: 16px;
            height: 16px;
        }
    }
}
.btn,
.btn:active,
.btn:hover,
.btn__del {
    text-decoration: none;
    box-sizing: border-box;
    width: 200px;
    height: 60px;
    padding: 10px auto;
    background: $Yel;
    border-radius: 79px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 12px;
    cursor: pointer;
    p {
        text-decoration: none;
        color: $White;
        font-weight: 700;

        line-height: 17px;
    }
    img {
        width: 15px !important;
        height: 15px !important;
    }
}
.btn__del {
    width: 60px;
    height: 60px;
    img {
        width: 17px;
        height: 19px;
    }
}
.toCart__btn {
    min-width: 148px;
    padding: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    font-weight: 700;
    font-size: 10px;
    color: $White;
    background: $Yel;
    border-radius: 80px;
    text-align: center;
    text-transform: uppercase;
    border: 0px solid $Black;
    gap: 5px;
    cursor: pointer;
    img {
        width: 20px !important;
        height: 16px;
    }
}
.good__info,
.good__size {
    width: 100%;
    display: flex;
    justify-content: start;
    gap: 5px;
}
.good__size {
    align-items: center;
    flex-direction: row;
    color: $Gray;
    opacity: 0.6;
    font-size: 12px;
}
.good__info {
    align-items: start;
    flex-direction: column;
}
.count {
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    gap: 20px;
    .count__change {
        padding: auto 14px;
        width: 34px;
        height: 23px;
        background: $GradYel;
        border-radius: 100%;
        border: none;
        cursor: pointer;
    }
    .count__value {
        text-align: center;
        width: 34px;
        height: 23px;
        border-radius: 100%;
        border: none;
    }
}
.Cart__box,
.Cart__box:active,
.Cart__box:hover {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $Black;
    gap: 28px;
    cursor: pointer;
    .Cart__img {
        position: relative;
        border: 0px solid $Black;
        background: $White;
        cursor: pointer;
        img {
            width: 40px;
            height: 29px;
        }
        .sum__position {
            position: absolute;
            width: 22px;
            height: 22px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: $Yel;
            border: 3px solid $White;
            border-radius: 50%;
            color: $White;
            font-weight: 700;
            font-size: 12px;
            top: -10px;
            left: 32px;
        }
    }
}

@media (max-width: 900px) {
    // Текст //
    p.back {
        position: relative;
        display: block;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 10px;
        width: 100%;
        text-align: start;
        line-height: 120%;
        margin-left: 152px;
        margin-top: 30px;
    }
    p.back::before {
        content: "<";
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 32px;
        height: 32px;
        position: absolute;
        top: -10px;
        left: -37px;
        border-radius: 50%;
        background: $Yel;
        opacity: 0.5;
    }
    p.stock {
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
    }
    p.description {
        display: none;
    }
    p.thanks__text {
        font-weight: 300;
        font-size: 12px;
        line-height: 120%;
        color: $Gray;
    }
    h1.Page__header {
        font-weight: 500;
        font-size: 20px;
        line-height: 120%;
        text-transform: uppercase;
    }
    h1.content__name,
    h3.content__subheader {
        font-weight: 800;
        font-size: 16px;
        line-height: 150%;
    }
    h2.Page__subheader {
        position: relative;
        width: 100%;
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        margin-top: 20px;
    }
    h2.Page__subheader::after {
        content: "<";
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        rotate: -90deg;
        width: 32px;
        height: 32px;
        position: absolute;
        top: -4px;
        right: 0;
        border-radius: 50%;
        background: $Yel;
        opacity: 0.5;
    }
    h2.price {
        font-weight: 800;
        font-size: 20px;
        line-height: 130%;
    }
    h3.content__subheader::after {
        content: "▼";
        width: 2px;
        height: 2px;
        margin-left: 5px;
    }

    // Разметка страницы //
    .Header {
        .Header__Up {
            display: none;
        }
        .Header__Down {
            .btn__menu {
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                background: $Yel;
                border-radius: 50%;
                padding: 12px;
                img {
                    width: 8px;
                    height: 7px;
                }
            }
            .Header__content {
                padding: 15px 15px;
                .btn,
                .Header__call,
                .inp__box,
                .Cart__text {
                    display: none;
                }
            }
        }
        .Mobile__Down {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: $Black;

            .btn,
            .inp__box {
                gap: 5px;
                width: 50%;
                padding: 13px auto;
                background: $White;
                border: 1px solid rgba($Gray, 0.2);
                background: $White;
                border-radius: 0;
                .inp__icon {
                    background: $White;
                    text-align: center;
                    display: flex;
                    justify-content: baseline;
                    align-items: center;
                }
                p {
                    color: $Black;
                }
            }
        }
    }
    .UpFilter {
        padding: 25px 15px 0px 15px;
        margin-bottom: 0px;
        margin-top: 0;

        .UpFilter__sort {
            width: 100%;
            justify-content: start;
            align-items: start;
            flex-direction: column;
            gap: 15px;
            .sort {
                display: flex;
            }
        }
        ul.Filter__list {
            display: flex;
            flex-direction: column;
            align-items: start;
            gap: 5;
            li {
                width: 100%;
                text-align: start;
                padding: 0;
            }
        }
    }
    .SideMenu {
        padding: 15px;
        margin: 0;
        width: 100%;
        display: flex;
        margin-top: 35px;

        h3.positions__subheader {
            display: none;
        }
        .SideMenu__filter {
            width: 100%;
            .check__filter {
                width: 100%;
            }
        }

        .SideMenu__btns {
            width: 100%;
            padding: 0;
        }
        .SideMenu__positions {
            display: none;
        }
    }
    .Page__goods {
        width: 100%;
        display: flex;
        .Goods {
            justify-content: center;
        }
    }
    .Catalog {
        padding: 15px;
        margin: 0;
        .SideMenu {
            display: none;
        }
    }

    .GoodsPage {
        margin: 0;
        padding: 0 15px;
        gap: 0;
        .Goods__content {
            padding: 0;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 15px;
            img {
                max-width: 290px;
                max-height: 200px;
            }
            .content__info {
                width: 100%;
                .good__size {
                    display: none;
                }
                .content__price {
                    width: 100%;
                    justify-content: start;
                    align-items: baseline;
                    flex-wrap: wrap;
                    gap: 25px;
                    .toCart__btn {
                        min-width: 184px;
                        min-height: 60px;
                        margin-right: 120px;
                    }
                }
                .content__action {
                    width: 100%;
                    padding: 0;
                    position: relative;
                    justify-content: start;
                    align-items: start;
                    flex-wrap: wrap;
                    gap: 60px;

                    img[alt="Поделиться"] {
                        position: absolute;
                        top: -75px;
                        right: 20px;
                    }
                    .btn {
                        width: 100%;
                    }
                }
                .good__info:last-child {
                    display: none;
                }
            }
        }
    }

    .CartPage {
        padding: 0 15px;
        gap: 0px;
        .Cart__position {
            padding: 20px 0;
            align-items: baseline;
            flex-wrap: wrap;
            gap: 10px;
            border-top: 1px dashed rgba($Gray, 0.2);
            border-bottom: 1px dashed rgba($Gray, 0.2);
            img {
                margin: auto;
                max-width: 290px;
                width: auto;
                max-height: 200px;
                height: auto;
            }
            .Cart__info {
                width: 100%;
                margin: 0;
                .good__size,
                .description {
                    width: 100%;
                    flex-wrap: wrap;
                    display: flex;
                }
            }
            .count {
                padding: 0 7%;
                border-left: 0px dashed rgba($Gray, 0.2);
                border-right: 0px dashed rgba($Gray, 0.2);
            }
            .price {
                font-size: 16px;
                padding: 0 7%;
                border-left: 1px dashed rgba($Gray, 0.2);
            }
            .btn__del {
                width: 60px;
                height: 60px;
                margin-left: 0px;
                img {
                    width: 17px;
                    height: 18px;
                    margin: auto;
                }
            }
        }
        .Cart__total {
            align-items: center;
            flex-direction: column-reverse;
            margin-bottom: 50px;
            margin-top: 25px;
            gap: 25px;
        }
    }
    .Thanks {
        .Thanks__box {
            width: 80%;
            height: 30%;
        }
    }
    .Footer {
        height: 100%;
        padding: 15px;
        box-shadow: 0px 15px 70px -11px rgba($SemiBlack, 0.04);
        .Mobile__footer {
            width: 100%;
            display: flex;
            justify-content: baseline;
            align-items: start;
            flex-direction: column;
            gap: 15px;
            color: $White;

            .Mobile__footer__box {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: start;
                flex-direction: row;
                text-align: start;
                flex-wrap: nowrap;
                .logo__box {
                    scale: 0.7;
                }
                .btn {
                    margin: 13px;
                    width: 149px;
                    height: 40px;
                    p {
                        font-size: 12px;
                    }
                    img {
                        width: 8px;
                        height: 9px;
                    }
                }
                .inp__box {
                    width: 100%;
                }
                h3 {
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 150%;
                    margin-bottom: 25px;
                }
                p {
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 150%;
                }
                ul {
                    margin-left: 0;
                    padding-left: 0;

                    li {
                        list-style-type: none;
                        margin-bottom: 20px;
                    }
                }
                .call__text {
                    margin-bottom: 25px;
                }
                .Icon__Box {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: row;
                    gap: 5px;
                    img.Social {
                        width: 39px;
                        height: 39px;
                    }
                    img.PayCard {
                        width: 61px;
                        height: 39px;
                    }
                }
            }
        }
        .Footer__col {
            display: none;
        }
    }

    // Универсальные блоки //
    .map__nav {
        margin: 0;
        ul {
            display: none;
        }
    }
    .count {
        gap: 15px;
        .count__change {
            padding: 4px 14px;
        }
        .count__value {
            padding: 4px 14px;
        }
    }
}
